import React, {useEffect, useState, useContext} from 'react'
import $ from 'jquery'
import Glide from '@glidejs/glide'
import CardProduct from './CardProduct'

export default function Product() {

    let [active] = useState("bg-green-500 text-white")
    let [inactive] = useState('bg-white text-green-500')
    let [link1, setLink1] = useState(active)
    let [link2, setLink2] = useState(inactive)
    let [link3, setLink3] = useState(inactive)
    let [link4, setLink4] = useState(inactive)
    let [link5, setLink5] = useState(inactive)
    let [link6, setLink6] = useState(inactive)
    let [showPro, setPro] = useState(true)
    let [showSet, setSet] = useState(false)
    let [showOpt, setOpt] = useState(false)

    let toggleLink1 = () => 
        setLink1(active) || setLink2(inactive) ||
        setLink3(inactive) || setLink4(inactive) ||
        setLink5(inactive) || setLink6(inactive) ||
        setPro(true) || setSet(false) || setOpt(false)

    let toggleLink2 = () => 
        setLink1(inactive) || setLink2(active) ||
        setLink3(inactive) || setLink4(inactive) ||
        setLink5(inactive) || setLink6(inactive) ||
        setPro(false) || setSet(true) || setOpt(false)

    let toggleLink3 = () => 
        setLink1(inactive) || setLink2(inactive) ||
        setLink3(active) || setLink4(inactive) ||
        setLink5(inactive) || setLink6(inactive) ||
        setPro(false) || setSet(false) || setOpt(true)

    let toggleLink4 = () => 
        setLink1(inactive) || setLink2(inactive) ||
        setLink3(inactive) || setLink4(active) ||
        setLink5(inactive) || setLink6(inactive) ||
        setPro(false) || setSet(false) || setOpt(true)

    let toggleLink5 = () => 
        setLink1(inactive) || setLink2(inactive) ||
        setLink3(inactive) || setLink4(inactive) ||
        setLink5(active) || setLink6(inactive) ||
        setPro(false) || setSet(false) || setOpt(true)

    let toggleLink6 = () => 
        setLink1(inactive) || setLink2(inactive) ||
        setLink3(inactive) || setLink4(inactive) ||
        setLink5(inactive) || setLink6(active) ||
        setPro(false) || setSet(false) || setOpt(true)

    // useEffect(() => {
    //     $(document).on('click', 'ul li', function(){
    //         $(this).addClass('text-white bg-green-500').siblings().removeClass('text-white bg-green-500');
    //         $(this).removeClass('bg-white text-green-500').siblings().addClass('bg-white text-green-500');
    //     })
    // })

    return (
        <div className="container mx-auto px-4">
            <div className="w-full">
                <h1 className="font-openSans font-bold text-2xl text-center">
                    Top Products
                </h1>
                <h1 className="text-center text-sm font-nunito my-2 mx-12 md:mx-auto">
                    We source directly from multiple factories to ensure the best quality and lowest prices.
                </h1>
                <ul className="flex mb-0 list-none md:flex-row md:flex-wrap pt-8 pb-4 overflow-x-auto" role="tablist">
                    <li className="mr-2 flex-auto text-center">
                        <a role="tablist" href="#link1" onClick={toggleLink1}
                            className={`text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block ${link1}`}>
                            Profile
                        </a>
                    </li>
                    <li className="mr-2 flex-auto text-center">
                        <a role="tablist" href="#link2" onClick={toggleLink2}
                            className={`text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block ${link2}`}>
                            Settings
                        </a>
                    </li>
                    <li className="mr-2 flex-auto text-center">
                        <a role="tablist" href="#link3" onClick={toggleLink3}
                            className={`text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block ${link3}`}>
                            Options
                        </a>
                    </li>
                    <li className="mr-2 flex-auto text-center">
                        <a role="tablist" href="#link4" onClick={toggleLink4}
                            className={`text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block ${link4}`}>
                            Options
                        </a>
                    </li>
                    <li className="mr-2 flex-auto text-center">
                        <a role="tablist" href="#link5" onClick={toggleLink5}
                            className={`text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block ${link5}`}>
                            Options
                        </a>
                    </li>
                    <li className="mr-2 flex-auto text-center">
                        <a role="tablist" href="#link6" onClick={toggleLink6}
                            className={`text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block ${link6}`}>
                            Options
                        </a>
                    </li>
                </ul>
                <div className="py-5">
                    { showPro ? <Profile /> : null}
                    { showSet ? <Settings /> : null}
                    { showOpt ? <Options /> : null}
                </div>
            </div>
        </div>
    )
}

const Profile = () =>{

    let slider = new Glide('.products', {
        type: 'slider',
        perView: 6,
        peek:{
            before:0,
            after:0
        },
        rewind:false,
        bound:true,
        animationTimingFunc:"ease",
        animationDuration:1000,
        swipeThreshold:50,
        dragThreshold:10,
        breakpoints:{
            360:{
                perView:2
            },
            640:{
                perView:2
            },
            768:{
                perView:4
            },
            1024:{
                perView:5
            },
            1280:{
                perView:6
            }
        }
    })

    useEffect(() => {
        slider.mount()
    }, [slider])

    return(
        <div className="glide products container mx-auto overflow-x-auto">
            <div className="glide__track" data-glide-el="track">
                <div className="glide__slides flex">
                    <div className="glide_slide">
                        <CardProduct/>
                    </div>
                    <div className="glide_slide">
                        <CardProduct/>
                    </div>
                    <div className="glide_slide">
                        <CardProduct/>
                    </div>
                    <div className="glide_slide">
                        <CardProduct/>
                    </div>
                    <div className="glide_slide">
                        <CardProduct/>
                    </div>
                    <div className="glide_slide">
                        <CardProduct/>
                    </div>
                    <div className="glide_slide">
                        <CardProduct/>
                    </div>
                </div>
            </div>
        </div>
    )
}

const Settings = () =>{
    return(
        <div className="space-y-5 mx-auto">
            <p className="font-nunito text-base">
            Completely synergize resource taxing relationships via premier niche markets. Professionally cultivate one-to-one customer service with robust ideas.
            </p>
            <p className="font-nunito text-base">
            Dynamically innovate resource-leveling customer service for state of the art customer service.
            </p>
        </div>
    )
}

const Options = () =>{
    return(
        <div className="space-y-5 mx-auto">
            <p className="font-nunito text-base">
                Efficiently unleash cross-media information without cross-media value. Quickly maximize timely deliverables for real-time schemas.
            </p>
            <p className="font-nunito text-base">
                Dramatically maintain clicks-and-mortar solutions without functional solutions.
            </p>
        </div>
    )
}